.bottom-nav {
  display: flex;
  align-items: center;
  margin-right: 50px;

  @media screen and (max-width: @1024) {
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
  }

  @media screen and (max-width: @576) {
    margin-right: 0;
  }
}

.bottom-nav__list--left {
  @media screen and (max-width: @1024) {
    order: 2;
    margin-top: 30px;
  }
}

.bottom-nav__list--right {
  @media screen and (max-width: @1024) {
    order: 3;
  }
}

.bottom-nav__logo {
  margin-right: 20px;
  margin-left: 20px;

  @media screen and (max-width: @1024) {
    margin-left: 0;
    margin-right: 0;
    order: 1;
  }
}

.bottom-nav__list {
  display: flex;

  @media screen and (max-width: @1024) {
    flex-direction: column;
    display: none;
  }
}

.bottom-nav__item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }

  @media screen and (max-width: @1024) {
    margin-bottom: 20px;
  }
}

.bottom-nav__link {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;

  @media screen and (max-width: @1440) {
    font-size: 18px;
  }

  @media screen and (max-width: @1366) {
    font-size: 14px;
  }

  @media screen and (max-width: @1200) {
    font-size: 12px;
  }

  @media screen and (max-width: @1024) {
    font-size: 24px;
  }
}
