.main-nav  {
  @media screen and (max-width: @768) {
    display: none;
  }
}

.main-nav__list {
  display: flex;

  @media screen and (max-width: @768) {
    flex-direction: column;
  }
}

.main-nav__item {
  margin-right: 20px;

  @media screen and (max-width: @768) {
    margin-bottom: 20px;
  }

  &:last-child {
    margin-right: 0;
  }
}

.main-nav__link {
  font-size: 14px;
  line-height: 16px;

  @media screen and (max-width: @768) {
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
  }
}

.main-nav--footer {
  @media screen and (max-width: @768) {
    display: block;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .main-nav__link {
    color: @white;

    &:hover {
      opacity: 0.8;
    }

    &:active,
    &:focus {
      opacity: 0.6;
    }
  }
}
