.header {
  a {
    color: @black;

    &:hover {
      opacity: 0.8;
    }

    &:active,
    &:focus {
      opacity: 0.6;
    }
  }
}

.header__wrapper-top {
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
}

.header__wrapper-top-inner {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;

  @media screen and (max-width: @1366) {
    padding-left: 40px;
    padding-right: 40px;
  }

  @media screen and (max-width: @1024) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (max-width: @768) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.header__top-nav {
  margin-left: auto;

  @media screen and (max-width: @768) {
    margin-top: 30px;
    margin-left: 0;
    margin-right: auto;
  }
}

.header__swirch {
  width: 25px;
  height: 14px;
  background-color: blue;
  margin-left: 40px;

  @media screen and (max-width: @768) {
    margin-left: 0;
    margin-top: 15px;
  }
}

.header__wrapper-bottom {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 19px;
  padding-bottom: 20px;

  @media screen and (max-width: @1366) {
    padding-left: 40px;
    padding-right: 40px;
  }

  @media screen and (max-width: @1024) {
    align-items: flex-start;
  }

  @media screen and (max-width: @1024) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.header__tel {
  // transform: translateX(calc(-100% - 70px));
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}

.header__burger {
  display: none;
  padding: 0;
  margin-left: 20px;
  outline: none;
  border: none;
  background-color: transparent;

  @media screen and (max-width: @1024) {
    display: block;
  }

  @media screen and (max-width: @768) {
    position: absolute;
    top: 10px;
    right: 20px;
  }

  .header__svg-burger {
    display: block;
  }

  .header__svg-close {
    display: none;
  }
}

.header__burger--active {
  .header__svg-burger {
    display: none;
  }

  .header__svg-close {
    display: block;
  }
}
