.slogan() {
  font-family: @jost;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;

  @media screen and (max-width: @768) {
    font-size: 14px;
    line-height: 20px;
  }
}

.small-title {
  font-family: @jost;
  font-weight: 500;
  font-size: 40px;
  line-height: 58px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;

  @media screen and (max-width: @768) {
    font-size: 30px;
    line-height: 48px;
  }
}
