.user-list {
  display: flex;
  align-items: center;

  @media screen and (max-width: @1024) {
    margin-top: 18px;
  }
}

.user-list__item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
    min-width: 46px;

    @media screen and (max-width: @1024) {
      min-width: 56px;
    }
  }

  @media screen and (max-width: @1366) {
    margin-right: 10px;

    svg {
      transform: scale(0.75);
    }
  }

  @media screen and (max-width: @1024) {
    margin-right: 20px;

    svg {
      transform: scale(1.1);
    }
  }
}

.user-list__link-count {
  display: inline-block;
  vertical-align: top;
  background-image: @gradient;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-top: 4px;
  padding-left: 7px;
  padding-top: 2px;
  font-size: 12px;
}
