.footer {
  background-color: @black;
}

.footer__wrapper {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;

  @media screen and (max-width: @768) {
    padding-left: 20px;
    padding-right: 20px;
    flex-direction: column;
    align-items: flex-start;
  }
}
